<template>
  <a-spin :spinning="loading">
    <a-form-model
      ref="form"
      :model="entity"
      :rules="rules"
      autocomplete="off"
      layout="vertical"
    >
      <div class="card">
        <a-row :gutter="24">
          <a-col :sm="6">
            <!-- 设备名称 -->
            <a-form-model-item
              label="设备名称"
              prop="resourceName"
              ref="resourceName"
            >
              <a-input
                v-model="entity.resourceName"
                placeholder="请输入设备名称"
                name="resourceName"
                @change="inChange"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 所属设备家族 -->
            <a-form-model-item
              label="所属设备家族"
              prop="resourceFamilyId"
              ref="resourceFamilyId"
            >
              <a-select
                v-model="entity.resourceFamilyId"
                placeholder="请选择所属设备家族"
                name="resourceFamilyId"
                @change="onFamilyChange"
              >
                <a-select-option v-for="item in familyList" :key="item.id" :value="item.id">
                  {{ item.name }}
                </a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 所属设备组 -->
            <a-form-model-item
              label="所属设备组"
              prop="resourceGroupId"
              ref="resourceGroupId"
            >
              <a-select
                v-model="entity.resourceGroupId"
                placeholder="请选择所属设备组"
                name="resourceGroupId"
                :disabled="!entity.resourceFamilyId"
              >
                <a-select-option v-for="item in groupList" :key="item.id" :value="item.id">
                  {{ item.name }}
                </a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- IP地址 -->
            <a-form-model-item
              label="IP地址"
              prop="ipAddress"
              ref="ipAddress"
            >
              <a-input
                v-model="entity.ipAddress"
                placeholder="请输入IP地址"
                name="ipAddress"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 设备型号 -->
            <a-form-model-item
              label="设备型号"
              prop="model"
              ref="model"
            >
              <a-input
                v-model="entity.model"
                placeholder="请输入设备型号"
                name="model"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 序列号 -->
            <a-form-model-item
              label="序列号"
              prop="serialNumber"
              ref="serialNumber"
            >
              <a-input
                v-model="entity.serialNumber"
                placeholder="请输入序列号"
                name="serialNumber"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 设备状态 -->
            <a-form-model-item
              label="设备状态"
              prop="status"
              ref="status"
            >
              <a-select
                v-model="entity.status"
                placeholder="请选择状态"
                name="status"
              >
                <a-select-option value="在线">在线</a-select-option>
                <a-select-option value="离线">离线</a-select-option>
                <a-select-option value="维护中">维护中</a-select-option>
                <a-select-option value="故障">故障</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 制造商 -->
            <a-form-model-item
              label="制造商"
              prop="manufacturer"
              ref="manufacturer"
            >
              <a-input
                v-model="entity.manufacturer"
                placeholder="请输入制造商"
                name="manufacturer"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="12">
            <!-- 描述信息 -->
            <a-form-model-item label="描述" prop="description">
              <a-textarea
                v-model:value="entity.description"
                placeholder="请输入描述信息"
                :rows="4"
                :max-length="1000"
                name="description"
              />
            </a-form-model-item>
          </a-col>
        </a-row>
      </div>
      <g-collapse headerName="详细信息">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="6">
              <!-- 购买日期 -->
              <a-form-model-item label="购买日期" prop="purchaseDate">
                <a-date-picker
                  v-model="entity.purchaseDate"
                  placeholder="请选择购买日期"
                  style="width: 100%"
                />
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 保修期至 -->
              <a-form-model-item label="保修期至" prop="warrantyDate">
                <a-date-picker
                  v-model="entity.warrantyDate"
                  placeholder="请选择保修期至"
                  style="width: 100%"
                />
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 创建人 -->
              <a-form-model-item label="创建人">
                <span>{{ entity.createdBy || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 创建时间 -->
              <a-form-model-item label="创建时间">
                <span>{{ entity.createdTime || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 修改人 -->
              <a-form-model-item label="修改人">
                <span>{{ entity.modifiedBy || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 修改时间 -->
              <a-form-model-item label="修改时间">
                <span>{{ entity.modifiedTime || '未知' }}</span>
              </a-form-model-item>
            </a-col>
          </a-row>
        </template>
      </g-collapse>
    </a-form-model>
  </a-spin>
</template>

<script>
import { GNdoPageEditBase } from "@/utils";
export default {
  name: "CreateOrEditResource",
  mixins: [GNdoPageEditBase],
  data() {
    return {
      entity: {
        resourceName: "",
        resourceFamilyId: "",
        resourceGroupId: "",
        ipAddress: "",
        model: "",
        serialNumber: "",
        status: "在线",
        manufacturer: "",
        description: "",
        purchaseDate: null,
        warrantyDate: null,
        createdBy: "",
        createdTime: "",
        modifiedBy: "",
        modifiedTime: ""
      },
      familyList: [], // 设备家族列表
      groupList: [], // 设备组列表
      rules: {
        resourceName: [
          { required: true, message: "设备名称不能为空", trigger: "change" },
          {
            pattern: /^[^\s]+(\s+[^\s]+)*$/,
            trigger: "change",
            message: "名称格式不正确，不能包含前后空格",
          },
        ],
        resourceFamilyId: [
          { required: true, message: "所属设备家族不能为空", trigger: "change" }
        ],
        resourceGroupId: [
          { required: true, message: "所属设备组不能为空", trigger: "change" }
        ],
        ipAddress: [
          { required: true, message: "IP地址不能为空", trigger: "change" },
          {
            pattern: /^(\d{1,3}\.){3}\d{1,3}$/,
            message: "IP地址格式不正确",
            trigger: "change"
          }
        ],
        serialNumber: [
          { required: true, message: "序列号不能为空", trigger: "change" }
        ],
        status: [
          { required: true, message: "设备状态不能为空", trigger: "change" }
        ]
      },
    };
  },

  mounted() {
    this.getFamilyList();
  },

  methods: {
    // 获取设备家族列表
    getFamilyList() {
      // 模拟获取设备家族列表数据
      setTimeout(() => {
        this.familyList = [
          { id: 1, name: "交换机" },
          { id: 2, name: "路由器" },
          { id: 3, name: "服务器" },
          { id: 4, name: "存储设备" },
          { id: 5, name: "防火墙" }
        ];
      }, 300);
    },
    
    // 当设备家族变更时获取对应的设备组列表
    onFamilyChange(value) {
      this.entity.resourceGroupId = "";
      this.getGroupList(value);
    },
    
    // 获取设备组列表
    getGroupList(familyId) {
      if (!familyId) return;
      
      // 模拟获取设备组列表数据
      setTimeout(() => {
        // 根据设备家族筛选设备组
        const groups = [
          { id: 1, name: "核心交换组", familyId: 1 },
          { id: 2, name: "接入交换组", familyId: 1 },
          { id: 3, name: "边界路由组", familyId: 2 },
          { id: 4, name: "核心路由组", familyId: 2 },
          { id: 5, name: "应用服务器组", familyId: 3 },
          { id: 6, name: "数据库服务器组", familyId: 3 },
          { id: 7, name: "主存储组", familyId: 4 },
          { id: 8, name: "备份存储组", familyId: 4 },
          { id: 9, name: "边界防火墙组", familyId: 5 },
          { id: 10, name: "内网防火墙组", familyId: 5 }
        ];
        
        this.groupList = groups.filter(group => group.familyId === familyId);
      }, 300);
    },
    
    // 初始化
    init() {
      console.log(this.id, "初始化");
      this.loading = true;
      if (this.id) {
        // 模拟请求获取设备数据
        setTimeout(() => {
          this.loading = false;

          this.entity = {
            resourceName: "核心交换机01",
            resourceFamilyId: 1,
            resourceGroupId: 1,
            ipAddress: "192.168.1.1",
            model: "S5820-32C-EI",
            serialNumber: "21023ABCDE12345",
            status: "在线",
            manufacturer: "华为",
            description: "数据中心核心交换设备，负责汇聚网络流量",
            purchaseDate: "2025-01-15",
            warrantyDate: "2028-01-15",
            createdBy: "admin",
            createdTime: "2025-03-18 10:30",
            modifiedBy: "admin",
            modifiedTime: "2025-03-18 11:55"
          };
          
          // 加载设备组列表
          this.getGroupList(this.entity.resourceFamilyId);
        }, 500);
      } else {
        this.reset();
        this.loading = false;
      }
    },
    
    resetFormData() {
      // 重置表单绑定的数据源
      this.entity = {
        resourceName: "",
        resourceFamilyId: "",
        resourceGroupId: "",
        ipAddress: "",
        model: "",
        serialNumber: "",
        status: "在线",
        manufacturer: "",
        description: "",
        purchaseDate: null,
        warrantyDate: null,
        createdBy: "",
        createdTime: "",
        modifiedBy: "",
        modifiedTime: ""
      };
      this.groupList = [];
    },
    
    async save() {
      // 保存 校验
      let res = await this.checkError();
      if (res) {
        this.loading = true;
        setTimeout(() => {
          this.loading = false;
          this.saveSuccess(this.entity.resourceName);
          this.handleSaveEndBackView(this.id);
          this.refreshParent({
            reset: null,
          });
        }, 500);
      }
    },
    
    inChange(e){
      console.log(e.target.value);
    }
  },
};
</script>

<style lang="scss" scoped></style> 